.coherence {
  position: relative;
  width: 34.22%;
  height: 100%;
  border-right: solid #707070 3px;
  background: radial-gradient(#515151, #000000);
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
}

.containerCoherence {
  height: 80%;
  width: 100%;
  display: flex;
  justify-content: center;
  /* align-items: center; */
  flex-direction: row;
  padding-left: 10px;
  padding-right: 10px;
}

.containerCoherence .arrowLeft {
  align-self: center;
  cursor: pointer;
}

.containerCoherence .arrowRight {
  align-self: center;
  cursor: pointer;
}

.coherenceTitleText {
  font-size: 47px;
  font-weight: bold;
}

.coherenceDashboard svg {
  width: 97%;
  height: 100%;
}

.coherenceBtn {
  height: 19%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.coherenceBtn_circles {
  width: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 28px;
}

.coherenceBtn_circle {
  width: 13px;
  height: 12px;
  background-color: #b1b1b1;
  border: 1px solid #707070;
  border-radius: 50%;
}

.coherenceBtn_circleActive {
  width: 28px;
  height: 28px;
  background-color: #fff;
  border-radius: 50%;
}

.coherenceBtn_complete {
  width: 200px;
  color: white;
  font-size: 28px;
  background-color: #b1b1b1;
  border: 1px solid #707070;
  border-radius: 28px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.intakeInfo {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.intakeInfoText {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 85%;
}

.intakeInfoText_health,
.carePlaneInfoText {
  width: 76.47%;
  height: 95%;
  border: 1px solid #707070;
  background: radial-gradient(#515151, #000000);
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.carePlaneInfoText {
  justify-content: center;
}

.intakeInfoText_health .fas.fa-times {
  position: absolute;
  color: #fff;
  font-size: 24px;
  top: 15px;
  left: 15px;
}

.intakeInfoText_healthTitle,
.carePlaneTitle {
  font-size: 28px;
  letter-spacing: 1px;
}

.carePlaneTitle {
  padding-bottom: 10px;
}

.carePlaneInfo,
.notesInfo {
  width: 90%;
  /* height: 69%; */
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
  align-items: baseline;
}

.carePlaneInfo div {
  margin-bottom: 1%;
}

.carePlaneInfo div:last-child {
  margin-bottom: 0;
}

.carePlaneInfo .carePlaneInfo_item,
.notesInfo .notesInfo_item {
  display: flex;
  justify-content: baseline;
  align-items: baseline;
}

.notesInfo {
  height: 45%;
  display: flex;
  justify-content: space-around;
  align-items: baseline;
}

.notesInfo .notesInfo_item {
}

.notesBtnAdd {
  width: 204px;
  height: 50px;
  border-radius: 38px;
  border: 1px solid #fff;

  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.notesBtnAdd .text {
  font-size: 26px;
}

.notesBtnAdd svg {
  margin-right: 10px;
}

/* modal window */
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modalOpen {
  display: block;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modalContentNotes {
  width: 55%;
  height: 55vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContentNotes .modalText {
  width: 100%;
  height: 100%;
}

.modalContentNotes .btnsModal {
  width: 100%;
  justify-content: space-around;
}
.modalContentNotes .btnsModal .btnModalOk,
.modalContentNotes .btnsModal .btnModalCancel {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContentNotes .modalContentNotesText textarea::placeholder {
  color: #fff;
}

.notesBtnToggle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 352px;
  height: 44px;
  border-radius: 38px;
  background-color: #4e4e4e;
}

.notesBtnToggle .btn {
  width: 175px;
  height: 100%;
  font-size: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #707070;
  border-radius: 38px;
}

.notesBtnToggle .btnActive {
  width: 175px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 26px;
  background-color: #b1b1b1;
  color: #fff;
  border-radius: 38px;
}

.carePlaneInfo .carePlaneInfo_item .title,
.notesInfo .notesInfo_item .title {
  letter-spacing: 1.3px;
  font-weight: 100;
  margin-right: 10px;
  font-size: 28px;
  color: #707070;
}

.notesInfo {
  width: 95%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.notesInfo .notesInfo_item {
  width: 100%;
}
.notesInfo .notesInfo_item .title {
  width: 100%;
}

.notesInfo .notesInfo_item .title .deleteCross {
  /* margin-left: 20px; */
  cursor: pointer;
}

.carePlaneInfo .carePlaneInfo_item .text,
.notesInfo .notesInfo_item .text {
  font-size: 29px;
  letter-spacing: 1px;
  color: #ffffff;
}

.intakeInfoText_health .clientIntakeInfo {
  width: 95%;
  height: 270px;
  display: flex;
  /* justify-content: center; */
  align-items: left;
  flex-direction: column;
  visibility: visible;
  /* overflow: scroll; */
  overflow: visible;
  overflow-y: auto;
}

.notesInfoText {
  padding: 10px;
}

.notesInfoText .intakeInfoText_healthTitle,
.notesInfo,
.notesBtnAdd {
  margin-bottom: 10px;
}

.notesInfoText .notesBtnAdd {
  margin-top: 10px;
  margin-bottom: 20px;
}

.clientIntakeInfoBlock {
  position: absolute;
  visibility: hidden;
}

.clientIntakeInfo .containerResult,
.modalContentNotes .modalOpen {
  width: 95%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.clientIntakeInfo .containerResult .intakeTextAreaResult,
.modalOpen .textAreaChange {
  width: 95%;
  height: 100% !important;
  background-color: transparent;
  outline: none;
  color: #fff;
  font-size: 22px;
  border: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modalOpen .textAreaChange:placeholder {
  color: #fff;
  font-size: 22px;
}

.clientIntakeInfo div {
  font-size: 28px;
  color: #707070;
}

.clientIntakeInfo_item {
  font-size: 26px;
  color: #fff;
}

.itemsConditions {
  margin-right: 5px;
}

.intakeInfoText_healthBtn {
  /* position: absolute;
	bottom: 0; */
  font-size: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 354px;
  background-color: #4e4e4e;
  border-radius: 38px;

  margin-bottom: 15px;
}

.intake_btn {
  margin-right: 30px;
  width: 50%;
  color: #aaaaaa;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: ease-out all 0.1s;
}

.intake_btnActive {
  margin-right: 30px;
  width: 50%;
  background-color: #b1b1b1;
  color: #fff;
  border-radius: 38px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: ease-in all 0.2s;
}

.intakeInfoText_healthBtn div:last-child {
  margin-right: 0;
}

.intakeInfoText_healthBtn .intake_btn:last-child {
  margin-right: 0;
}

.intakeInfoText_results {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 18%;
  height: 100%;
}

.intakeInfoText_results .results {
  width: 121px;
  height: 121px;
  border: 1px solid #aef7ff;
  border-radius: 50%;
}
.intakeInfoText_results .results:last-child {
  margin-bottom: 0;
}

.results {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.results div {
  font-size: 23px;
  color: #aef7ff;
}

.intakeInfoAlpha {
  height: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 10px;
}

.intakeInfoAlpha div:nth-child(1) {
  margin-right: 25px;
}

.intakeInfoAlpha div:nth-child(2) {
  margin-right: 5px;
}

.intakeInfoAlpha_text {
  font-size: 43px;
}

.intakeInfoAlpha_letters {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.intakeInfoAlpha_letters .letter {
  font-size: 24px;
  color: #aef7ff;
}

.intakeInfoAlpha_dashboard {
  width: 68%;
}

.intakeInfoAlpha_dashboard svg {
  width: 100%;
}

@media (max-width: 1024px) {
  .coherenceTitleText {
    font-size: 37px;
    font-weight: bold;
    margin-top: 20px;
  }

  .coherenceDashboard {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .coherenceBtn .coherenceBtn_circles {
    width: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 18px;
  }

  .coherenceBtn .coherenceBtn_complete {
    width: 180px;
    font-size: 22px;
  }

  .intakeInfoText {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  .intakeInfoText_results {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 75%;
    padding-top: 10px;
    padding-bottom: 10px;
    order: 1;
  }

  .intakeInfoText_results .results {
    width: 90px;
    height: 90px;
    border: 1px solid #aef7ff;
    border-radius: 50%;
    font-size: 16px;
  }

  .intakeInfoText_results .results div {
    font-size: 18px;
  }

  .intakeInfoText_health {
    padding: 5px;
    height: 44.2vh;
    width: 90%;
    order: 2;
  }

  .carePlaneInfo {
    padding-bottom: 10px;
  }

  .carePlaneInfo .carePlaneInfo_item .title {
    font-size: 24px;
  }

  .intakeInfoText_healthBtn {
    width: 80%;
    font-size: 16px;
  }
  .intakeInfoAlpha_dashboard {
    /* width: auto; */
  }

  .carePlaneInfo div {
    margin-bottom: 0;
  }

  .clientIntakeInfo div {
    font-size: 18px;
  }

  .clientIntakeInfo_item {
    font-size: 14px;
  }

  .intakeInfoText_healthBtn {
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .intakeInfoAlpha {
    height: auto;
    width: 95%;
  }
}

@media (max-width: 891px) {
  .containerIntakeContent {
    height: 100%;
    align-items: center;
    flex-direction: column;
  }
  .coherence {
    width: 90%;
    height: 100%;
    /* border-right: none; */
  }

  .coherenceDashboard {
    width: 100%;
    height: 100%;
    margin-bottom: 1em;
  }

  .coherenceDashboard svg {
    width: 55%;
    height: 45%;
  }
}

@media (max-width: 540px) {
  .coherenceBtn_complete {
    font-size: 1.5rem;
  }
  .intakeInfoText_results {
    width: 90%;
  }

  .intakeInfoText_healthBtn {
    font-size: 1.05rem;
  }
}

@media (max-width: 445px) {
  .coherenceBtn_complete {
    font-size: 1.3rem;
  }

  .intakeInfoText_results {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 18%;
    height: 100%;
  }

  .intakeInfoText_results .results {
    width: 121px;
    height: 121px;
    border: 1px solid #aef7ff;
    border-radius: 50%;
    margin-bottom: 10px;
  }

  .intakeInfoText_healthBtn {
    font-size: 0.8rem;
  }

  .intakeInfoAlpha_text {
    font-size: 1em;
  }
}

@media (max-width: 376px) {
  .intakeInfoText_healthBtn {
    width: 90%;
  }

  .intakeInfoAlpha {
    flex-wrap: wrap;
    margin-top: 0.6em;
  }

  .intakeInfoAlpha div:nth-child(1) {
    width: 100%;
    margin: 0;
    text-align: center;
    font-size: 0.8em;
  }
  .intakeInfoAlpha_dashboard {
    width: 70%;
  }
  .intakeInfoAlpha_letters div:nth-child(1) {
    margin-right: 25px;
  }
  .intakeInfoAlpha_letters .letters {
    font-size: 2em;
  }
}
